.wrapper {
    display: flex;
    padding: 30rem;
    width: 100vw;
    height: 100vh;
    background-color: rgb(10, 10, 10);
}

.container {
    display: flex;
    width: 100%;
    height: 100%;
    background-color: rgba(255,255,255, 0.1);
    border-radius: 10rem;
    justify-content: space-evenly;
    align-items: center;
}

.logo {
    display: flex;
    height: 100%;
    width: fit-content;
    padding: 50rem;
}

.text {
    display: flex;
    font-size: 80rem;
    color: white;
    justify-content: center;
    align-items: center;
}